<template>
	<view class="box">
		<mescroll-uni ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
			<navigator :url="'/pages/member/message_detail?seqId='+item.seqId" class="message" v-for="(item,k) in data_list" :key="k">
				<view class="time">
					{{item.createDate}}
				</view>
				<view class="list-li">
					<view class="title">{{item.title}}</view>
					<view class="content">{{item.content}}
					</view>
				</view>
			</navigator>
		</mescroll-uni>
	</view>
</template>

<script>
	let notifyType = "";
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue";
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				data_list: [],
				upOption: {
					noMoreSize:5
				}
			}
		},
		onLoad(option) {
			notifyType = option.notifyType
		},
		components: {
			MescrollUni
		},
		methods: {
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oNotifyHead/findPageByNotifyType', {
					params: {
						notifyType: notifyType,
						pageno:pageNum,
						pagesize: pageSize
					}
				}).then((res) => {
					if(res.code&&res.code!=200){
						this.mescroll.endErr();
						return}
						let data = res;
						// 接口返回的当前页数据列表 (数组)
						let curPageData = data.list;
						// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
						let curPageLen = curPageData.length;
						// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
						let totalPage = data.pages;
					
						//设置列表数据
						if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
						this.data_list=this.data_list.concat(curPageData)
						// 请求成功,隐藏加载状态
						//方法一(推荐): 后台接口有返回列表的总页数 totalPage
						this.mescroll.endByPage(curPageLen, totalPage);
				})
			}
		}
	}
</script>

<style lang="scss">
	.time {
		width: 100%;
		height: 60rpx;
		line-height: 60rpx;
		text-align: center;
		color: #999;
		font-size: 20rpx;
	}

	.list-li {
		width: 620rpx;
		height: auto;
		background: rgba(255, 255, 255, 1);
		box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
		border-radius: 10rpx;
		padding: 40rpx 30rpx;
		margin: 0 auto;

		.title {
			font-size: 30rpx;
		}

		.content {
			font-size: 26rpx;
			color: #999;
			margin-top: 24rpx;
			line-height: 1.5;
		}
	}
</style>
